import {
  ADD_PRODUCT,
  REMOVE_PRODUCT,
  DECREASE_PRODUCT_QUANTITY,
  INCREASE_PRODUCT_QUANTITY,
} from './actionTypes';

const initialState = {
  products: [],
};

const findProductIndex = (products, product) =>
  products.findIndex(p => p.id === product.id);

const cartReducer = (state = initialState, action) => {
  switch (action.type) {
    case ADD_PRODUCT:
      return {
        ...state,
        products: [...state.products, action.payload],
      };
    case REMOVE_PRODUCT:
      return {
        ...state,
        products: state.products.filter(product => product !== action.payload),
      };
    case DECREASE_PRODUCT_QUANTITY: {
      const products = state.products;
      const productIndex = findProductIndex(products, action.payload);
      products[productIndex].quantity = action.payload.quantity - 1;

      return {
        ...state,
        products: [...products],
      };
    }
    case INCREASE_PRODUCT_QUANTITY: {
      const products = state.products;
      const productIndex = findProductIndex(products, action.payload);
      products[productIndex].quantity = action.payload.quantity + 1;

      return {
        ...state,
        products: [...products],
      };
    }
    default:
      return state;
  }
};

export {cartReducer};
